import common from '~/mixins/common'

export default {
  mixins: [common],
  methods: {
    logout () {
      this.$store.dispatch('logout')
    },
    async login () {
      const provider = new this.$fireModule.auth.GoogleAuthProvider()
      await this.$fire.auth.signInWithRedirect(provider)
    }
  }
}
