import { mapGetters } from 'vuex'

export default {
  computed: {
    ...mapGetters({
      isLoggedIn: 'isLoggedIn',
      user: 'user'
    })
  },
  methods: {
    logout () {
      this.$store.dispatch('logout')
    }
  }
}
